import addZeroToTime from '../../../utils/addZeroToTime';

const decreaseTime = (timeStr, subject, lowBorder, step) => {
  const time = Number(timeStr);

  if (time >= lowBorder) {
    return {
      type: subject,
      payload: addZeroToTime(time - step),
    };
  }

  return {
    type: subject,
    payload: addZeroToTime(time),
  };
};

export default decreaseTime;
