import addZeroToTime from '../../../utils/addZeroToTime';

const increaseTime = (timeStr, subject, lowBorder, highBorder, step) => {
  const time = Number(timeStr);

  if (time < highBorder && time >= lowBorder) {
    return {
      type: subject,
      payload: addZeroToTime(time + step),
    };
  }

  return {
    type: subject,
    payload: addZeroToTime(lowBorder),
  };
};

export default increaseTime;
