import React, { Component, createRef } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import addLabel from '../../HOC/addLabel';
import './TimeInput.scss';

class TimeInput extends Component {
  static propTypes = {
    onHoursChange: PropTypes.func.isRequired,
    onMinutesChange: PropTypes.func.isRequired,
    hours: PropTypes.string.isRequired,
    minutes: PropTypes.string.isRequired,
    increaseHours: PropTypes.func.isRequired,
    increaseMinutes: PropTypes.func.isRequired,
    decreaseHours: PropTypes.func.isRequired,
    decreaseMinutes: PropTypes.func.isRequired,
    limitError: PropTypes.bool.isRequired,
    timeError: PropTypes.oneOfType([
      PropTypes.bool,
      PropTypes.number, // 1 or 0
    ]).isRequired,
    isAllErrorsShown: PropTypes.bool.isRequired,
  }

  constructor(props) {
    super(props);
    this.hours = createRef();
    this.minutes = createRef();

    this.state = {
      focused: false,
    };
  }

  triggerFocus = () => {
    this.setState(prevState => ({
      focused: !prevState.focused,
    }));
  }

  handleKeyDown = (e, increase, decrease) => {
    switch (e.key) {
      case 'ArrowUp':
        increase();
        break;

      case 'ArrowDown':
        decrease();
        break;

      default:
        break;
    }
  }

  focusInput = (e) => {
    this.triggerFocus();
    e.currentTarget.select();
  }

  render() {
    const
      {
        onHoursChange,
        onMinutesChange,
        hours,
        minutes,
        timeError,
        limitError,
        isAllErrorsShown,
        increaseHours,
        increaseMinutes,
        decreaseHours,
        decreaseMinutes,
      } = this.props;

    const { focused } = this.state;
    const isError = (timeError || limitError) && isAllErrorsShown;

    return (
      <div
        className={`
          time-input 
          ${focused ? 'time-input--focused' : ''} 
          ${isError ? 'time-input--error' : ''}
        `}
      >
        <input
          type="text"
          value={hours}
          onChange={onHoursChange}
          className="time-input__invisible-input"
          onFocus={this.focusInput}
          onBlur={this.triggerFocus}
          onKeyDown={(e) => {
            this.handleKeyDown(e, increaseHours, decreaseHours);
          }}
          ref={this.hours}
        />

        <span className="time-input__colon">:</span>

        <input
          type="text"
          value={minutes}
          onChange={onMinutesChange}
          className="time-input__invisible-input"
          onFocus={this.focusInput}
          onBlur={this.triggerFocus}
          onKeyDown={(e) => {
            this.handleKeyDown(e, increaseMinutes, decreaseMinutes);
          }}
          ref={this.minutes}
        />
      </div>
    );
  }
}

const mapStateToProps = state => ({
  timeError: state.errorReducer.timeError,
  limitError: state.errorReducer.limitError,
  isAllErrorsShown: state.errorReducer.isAllErrorsShown,
});

const ConnectedComponent = connect(mapStateToProps)(TimeInput);

export default addLabel(ConnectedComponent);
